##公式
公式以·=·或·-·开头
###基本运算
选择单元格输入·=·，点击要计算的单元格或输入单元格的地址，输入运算符，输入对应的计算值，回车计算，比如·=A1-A2·
###合并单元格
·=A1&A2·，双引号表示插入分隔符·=A1&"，"&A2·
###条件判断
单条件：·=if(A1>=60, "及格", "不及格")·
多条件并且：·=if(and(A1>=60, A2="A"), "及格", "不及格")·
多条件或者：·=if(or(A1>=60, A2="A"), "及格", "不及格")·
条件嵌套：：·=if(A1<60, "不及格", if(A1<90, "及格", "优秀"))·
###相对引用和绝对引用
普通的公式就是相对引用，如·=A1-A2·，公式的值随着复制的变化而变化
给引用的单元格加上·$·就是绝对引用，如·=$A$1-$A$2·，无论这个公式在哪个单元格都是计算·A1-A2·

