##无法打印
注意主电脑睡眠休眠状态下无法打印
如果没有打印机驱动可以直接百度【打印机型号 驱动下载】或去打印机的官网下
如果发现连接上打印机却无法打印，可能是共享打印机的主电脑没有开启服务：计算机>管理>服务>将【Print Spooler】启动，如果已经启动了还不行就删掉设备再来一次
如果共享打印机的主电脑重启之后无法打印，可能是没有激活打印机，在主电脑上打印测试页之后再在其他电脑上打印试试
不行的话可能是主电脑重启后，其他电脑不能自动连接到主电脑，需要添加 windows 凭据：开始 > 控制面板 > 用户账户和家庭安全 > 凭据管理器 > 添加windows凭据 > Internet 地址或网络地址即主电脑的电脑名称，用户名即主电脑的账户名（没改默认是 Administrator），密码即主电脑的密码（没有就不填）

#共享文件
将电脑的文件或盘共享可以让其他电脑查看和修改，这需要在同一 局域网/WiFi 下才能连接
要共享电脑的操作：
!!
网络共享：网络和共享中心 > 高级共享设置 > 把【网络发现】【文件和打印机共享】【公用文件夹共享】启用，【密码保护】关闭
文件共享：右键要分享的盘或文件夹 > 共享 > 高级共享 > 高级共享 > 勾选【共享此文件夹】（可以设置共享名） > 权限 > 勾选【完全控制】
!!
访问共享的电脑：打开我的电脑或运行，在目录栏输入【\\\\共享电脑的名字或 IP 地址】
电脑名字在【我的电脑 > 右键 > 属性】中查看，电脑 IP 即 IPv4 地址，在【网络和共享中心 > 点击连接的网络 > 详细信息】中查看
也可以在我的电脑添加一个网络位置，点击浏览选择共享的电脑名，再选择对应的文件夹
&nbsp;
共享的文件不太好指定某台电脑才能访问，但是可以设置访问密码，在共享的电脑上：网络和共享中心 > 高级共享设置 > 开启密码保护
其他电脑访问时需要输入账号（共享电脑的用户名），注意不是电脑名，密码就是共享电脑的密码
注意共享的电脑需要开机才能访问

