###重启失败
开机显示·windows boot manager has been blocked by the current security policy·（windows 启动管理器已被当前安全策略阻止）
一般这种问题出现在 win10 或 win8 重装成 win7 的时候，win10 或 win8 系统开启了 UEFI 安全启动选项
以联想笔记本为例，刚开机时按 F12 是进入 Boot 界面，此时再重启电脑，刚开机时按 F2 进入 BIOS 界面，进行操作：
!!
Security 菜单：选择 Secure BOOT，改为 Disabled，表示关闭安全策略
Boot 菜单：
    选择 boot mode，改为 Legacy First 或 legacy support 或 AUTO，表示修改引导模式为传统优先或其他
    选择 boot priority，改为 Legacy First 或 legacy support 或 AUTO，表示修改引导优先级为传统优先或其他
    选择 Launch CSM（如果有这个选项），改为 Enabled
Exit 菜单：选择 OS Optimized Defaults，改为 OTHER OS，表示修改默认优先系统为其他系统
基本上都改为第二个选项就行了，完成后按 F10 保存退出即可
再次进入 pe 系统，选择快速分区，在左边选择 MBR 类型（win7 一般只支持 MBR，win8/10 以上才支持 GUID/GPT）
!!

###进不了pe系统
!!
在系统 BIOS 中设置·Devices>ATA Drive Setup>IDE·
试试其他的一键重装系统软件，在电脑上安装后一键下载系统安装包然后自动解压安装
